import React, { Component } from 'react'
import { ExternalLink } from 'react-external-link';
import Gith from './search.png';
import Tic from './tictac.png';
import Puzz from './puzzle.png';
import Chatt from './chat.jpg';
import './Project.css';



export default class Projects extends Component {
    render() {
        return (
            <div className >
               <div className = "link1">
               <ExternalLink className='picpro' href="https://abdelbassetaitbella.github.io/Search-engine-Abdelbasset/">
                      <p className='title1'>search engine</p>
                      <br></br>
                      <img className='picpro' src={Gith} />
                       
               </ExternalLink>
              </div>
               <div className="link2">
               <ExternalLink className='picpro1' href="https://play.google.com/store/apps/details?id=com.bellaaps.tictaccrazy1">
                       <p className= "title2">Tic Tac Toe Mobile</p>
                       <br></br>
                       <img className="picpro1" src={Tic} />
               </ExternalLink>
               </div>

               <div className='link3'>
               <ExternalLink className='picpro2' href="https://play.google.com/store/apps/details?id=com.bellaaps.puzzlecrazy">
                       <p className= "title3">Puzzle crazy Mobile</p>
                       <br></br>
                       <img className="picpro2" src={Puzz} />
               </ExternalLink>
               </div>

               <div className='link4'>
               <ExternalLink className='picpro3' href="https://github.com/abdelbassetaitbella/chatting-social">
                       <p className= "title4">Social Chat Rooms</p>
                       <br></br>
                       <img className="picpro3" src={Chatt} />
               </ExternalLink>
               </div>



               </div>

           
        )
    }
}
