import React, { Component } from 'react'
import slideSlider from './slideSlider';
import './slideSlider.css'

import {Img} from 'react-image'
import html  from './images/html.png';
import ajax from './images/ajax.png';
import css from './images/css.png';
import android from './images/android.png';
import bootstrap from './images/bootstap.png';
import jquery from './images/jquery.png';
import js from './images/js.png';
import laravel from './images/laravel.png';
import php from './images/php.png';
import react from './images/react.png';
import microsoft from './images/microsoft.png';
import cisco from './images/cisco.png';
import mysql from './images/mysql.png';





export default class Skills extends Component {
    render() {
        return (
            
                <div id="slider-container">
  <div id="slider-scroller">
    <div class="slider-item"><Img src={html} /></div>
    <div class="slider-item"><Img src={css} /></div>
    <div class="slider-item"><Img src={bootstrap} /></div>
    <div class="slider-item"><Img src={js} /></div>
    <div class="slider-item"><Img src={ajax} /></div>
    <div class="slider-item"><Img src={jquery} /></div>
    <div class="slider-item"><Img src={react} /></div>
    <div class="slider-item"><Img src={php} /></div>
    <div class="slider-item"><Img src={laravel} /></div>
    <div class="slider-item"><Img src={mysql} /></div>
    <div class="slider-item"><Img src={android} /></div>
    <div class="slider-item"><Img src={microsoft} /></div>
    <div class="slider-item"><Img src={cisco} /></div>
  </div>
 </div>
 
        )
    }
}
