import React from "react";
import { MDBContainer, MDBFooter } from "mdbreact";
import './Footer.css';

const FooterPage = () => {
  return (
    <MDBFooter color="blue" className="footer">
      <div className="footer-copyright text-center py-3">
        <MDBContainer fluid>
          &copy; {new Date().getFullYear()} Copyright: <a className="cp" href="https://www.linkedin.com/in/abdelbasset-ait-bella-b5b837195/"> Abdelbasset AIT BELLA </a>
        </MDBContainer>
      </div>
    </MDBFooter>
  );
}

export default FooterPage;